.main-slider {
  width: 100%;
  height: 800px;
  margin-top: $navbar-height;

  .slide-1, .slide-2, .slide-3 {
    background: {
      image: url(../img/slide-test.jpg);
      position: center center;
      size: cover;
      repeat: no-repeat; }
    height: 800px; }

  .slick-slide:focus {
    outline: none; }

  .slide-dots {
    -webkit-box-align: center;
    -webkit-box-pack: center;
    display: -webkit-box;

    li {
      position: relative;
      top: -50px;
      display: inline-block;
      padding: 0 5px;

      button {
        width: 10px;
        height: 10px;
        font-size: 0;
        border: none;
        background-color: rgba($white, .5);
        border-radius: 80%;
        transition: all .4s ease; }

      &.slick-active {
        button {
          background-color: $white; } } } } }

.slick-next, .slick-prev {
  display: none !important; }

.mouse-animate {
  position: absolute;
  top: 75%;
  left: 50%;
  transform: translateX(-50%); }
